import styled from '@emotion/styled'

import React, { FunctionComponent } from 'react'
import CategoryList from './CategoryList'
import PostList from './PostList'
import { PostListItemType } from 'types/PostItem.types'

type PostListProps = {
  posts: PostListItemType[]
}

const MainContentWrap = styled.div`
  max-width: 1200px;
  margin: 0 auto;
  background-color: #fff;
`

const CATEGORY_LIST = ['HTML', 'CSS', 'JAVASCRIPT','HTML', 'CSS', 'JAVASCRIPT','HTML', 'CSS', 'JAVASCRIPT','HTML', 'CSS', 'JAVASCRIPT']

const MainContent: FunctionComponent<PostListProps> = function({posts}){

  return (
    <MainContentWrap>
      <CategoryList selectedCategory="HTML" categoryList={CATEGORY_LIST} />
      <PostList posts={posts} />
    </MainContentWrap>
  )
}

export default MainContent